import styled from "styled-components";

export const QRcode = styled.div`
  position: relative;
  text-align: center;

  & > div.Qrcode-title {
    font-size: 24px;
    color: #1a1a1a;
    line-height: 33px;
    margin-bottom: 50px;
    margin-top: 3px;
  }

  & > div.Qrcode-content {
    div.Qrcode-img {
      width: 180px;
      height: 180px;
      background-color: #b8e5f8;
      margin: 0 auto 15px;
      line-height: 0;
    }
  }
`;

export const SwitchButton = styled.span`
  margin-left: 10px;
  font-weight: 600;
  color: #0084ff;
  cursor: pointer;
`;

export const Sign_Bg = styled.div`
  background-image: ${props => props.url};
  background-color: ${props => props.color};
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  padding-right: 15px;
`;

export const Sign_Content = styled.div`
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  min-height: 688px;
  height: calc(100% - 42px);
  box-sizing: border-box;
`;

export const LogoBanner = styled.div`
  background-color: transparent;
  padding-top: 32px !important;
  border-bottom: 0px;
  width: 100% !important;
  font-size: 24px;
  font-weight: 600;
  text-align: center;
  box-sizing: border-box;

  & > div {
    text-align: center;
    width: 100% !important;
    margin-left: auto;
    margin-right: auto;
  }
`;

export const ToHomePage = styled.a`
  text-decoration: none;
  color: rgb(51, 51, 51);
  background-color: transparent;
  text-align: center;

  & > img {
    height: 10%;
    width: 10%;
  }
`;

export const AuthCard = styled.div`
  box-sizing: border-box;
  margin-top: 16px !important;
  border-radius: 5px;
  border-top: 1px solid rgb(216, 222, 226);
  background-color: rgb(255, 255, 255);
  font-size: 14px;
  border-width: 0px 1px 1px;
  border-style: solid solid solid;
  border-color: rgb(216, 222, 226) rgb(216, 222, 226) rgb(216, 222, 226);
  border-image: initial;
  padding: 20px;
  min-width: 420px;
`;

export const FormHeader = styled.div`
  color: rgb(51, 51, 51);
  margin: 15px auto;
  text-align: center;
  text-shadow: none;
  border-width: 0px;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -0.5px;
`;

export const InputMask = styled.div`
  position: relative;
  border-bottom: 1px solid #ebebeb;
  margin-top: 12px;
  width: 100%;
  min-width: 280px;
  height: 48px;
  padding: 0;

  & input {
    height: 48px;
    width: 100%;
    color: #1a1a1a;
    line-height: 24px;
    flex: 1 1;
    padding: 0 0 0 2px;
    overflow: hidden;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    background: transparent;
    border: none;
    outline: none;
    resize: none;
  }

  & > button {
    color: #8590a6;
    background: none;
    -webkit-appearance: none;
    height: auto;
    padding: 0;
    background-color: transparent;
    border: none !important;
    position: absolute;
    top: 17px;
    right: 0;

    :focus {
      outline: none;
    }

    & > img {
      height: 20px;
      width: 24px;
    }
  }

  & #message {
    color: #175199;
    font-size: 15px;
  }
`;

export const Options = styled.div`
  margin-top: 12px;
  height: 20px;
  display: flex;
  justify-content: space-between;

  & > button {
    color: #175199;
    padding: 0;
    line-height: inherit;
    background-color: transparent;
    border: none;
    font-size: 15px;
    outline: none;
  }

  & #lost {
    float: right;
    color: #224080;
  }
`;

//Both
export const OtherOption = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 25px;
  margin-top: 16px;
  margin-bottom: 10px;
  text-align: center;

  & > span {
    display: inline-block;
    color: #8590a6;
    vertical-align: middle;
    & button {
      border: none;
      background: transparent;
    }
  }

  & > span:not(:last-child)::after {
    content: "•";
    white-space: pre;
    margin: 0 8px;
  }

  &.term {
    font-size: 13px;
    color: #b52828;

    & a {
      color: #8590a6;
    }

    & > span:not(:last-child)::after {
      content: "";
    }
  }
`;

export const Submit = styled.button`
  width: 100%;
  margin-top: 30px;
  height: 36px;
  color: #ffffff;
  background-color: #0084ff;
  border-color: #0084ff;
  display: inline-block;
  padding: 0 16px;
  font-size: 14px;
  line-height: 32px;
  border: 1px solid;
  border-radius: 3px;

  :hover {
    color: #6a9fcd !important;
  }
`;

export const Switch = styled.div`
  padding: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 100%;
  height: 58px;
  border-top: 1px solid #ebebeb;
  font-size: 16px;

  a {
    color: #0084ff;
  }
`;

export const Footer = styled.footer`
  font-size: 12px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 2px #999999;

  a {
    color: #ffffff;
    text-shadow: 0 1px 2px #999999;
  }

  div a:not(:last-child)::after,
  span:not(:last-child)::after {
    content: " • ";
    white-space: pre;
    display: inline-block;
  }
`;

export const ZhengFuInfo = styled.div`
  border-top: #ebebeb 1px solid;
  width: 420px;
  margin: 10px auto;
  padding-top: 10px;

  & > div {
    cursor: pointer;
    display: inline-block;
    position: relative;
    line-height: 26px;
    z-index: 0;
  }

  & div img {
    position: absolute;
    left: -34px;
    z-index: -1;
    width: 138px;
    height: 26px;
  }
`;

export const CountriesSelectContainer = styled.div`
  display: inline-block;
  float: left;
  min-width: 72px;
  position: relative;

  & > button {
    color: #8590a6;
    padding: 0;
    border: 1px solid transparent;
    height: 48px;
    text-align: left;
    box-sizing: border-box;
    background: none;
    outline: none;

    & > span {
      display: inline-flex;
      align-items: center;
      vertical-align: middle;
    }
  }
`;

export const Seperator = styled.span`
  float: left;
  width: 1px;
  height: 26px;
  background: #eaebeb;
  vertical-align: middle;
  margin: 12px 2px;
`;

export const RelativeWarp = styled.div`
  position: relative;
  overflow: hidden;
  padding-left: 12px;
`;

export const Err = styled.input`
  &::-webkit-input-placeholder {
    color: #d11111;
  }
  &::-moz-placeholder {
    color: #d11111;
  }
  &::-ms-input-placeholder {
    color: #d11111;
  }

  display: flex;
  position: absolute;
  top: 0px;
  margin-left: 2px;
  background: white !important;
  justify-content: center;
  flex-direction: column;
  display: none;
`;
