import React from 'react';
import * as CSS from './css';
import Arrow from '../../Static/UpDownArrow.svg';
import * as COM from './Components/';

const SignUp = (props)=>(
    <CSS.AuthCard>
        <CSS.FormHeader>
            Hello World
        </CSS.FormHeader>
        <CSS.InputMask>
            <CSS.CountriesSelectContainer>
                <button>
                    中国 +86
                    <span>
                        <img src={Arrow} alt="Arrow" />
                    </span>
                </button>
            </CSS.CountriesSelectContainer>
            <CSS.Seperator>&nbsp;</CSS.Seperator>
            <CSS.RelativeWarp>
                <COM.InputWithErr
                    id="id"
                    verification={props.verification.phone}
                    input={{
                        placeholder:"手机号",
                        value: props.state.User.id,
                        onChange: props.InputCtrl.InputHandle,
                    }}
                    err={{
                        placeholder:"手机号码格式错误",
                    }}
                />
            </CSS.RelativeWarp>
        </CSS.InputMask>
        <CSS.InputMask>
            <COM.InputWithErr 
                id="pwd"
                verification={props.verification.verific}
                input={{
                    placeholder: "验证码",
                    value: props.state.User.pwd,
                    onChange: props.InputCtrl.InputHandle,
                }}
                err={{}}
            />
            <button id="message" onClick={props.ClickHandle}>获取验证码</button>
        </CSS.InputMask>
        <CSS.Options>
            <button id="email" onClick={props.ClickHandle}>邮箱注册</button>
            <button id="lost" onClick={props.ClickHandle}>接收语音验证码</button>
        </CSS.Options>
        <CSS.Submit id="submit" onClick={props.ClickHandle}>注册</CSS.Submit>
        <CSS.OtherOption className="term">
            <span>
                注册即代表同意
                <a href="//www.zhihu.com/terms">《知乎协议》</a>
                <a href="//www.zhihu.com/term/privacy">《隐私保护指引》</a>
            </span>
            <a href="//www.zhihu.com/org/signup">注册机构号</a>
        </CSS.OtherOption>
        <CSS.Switch>
            {
                props.Switch()
            }
        </CSS.Switch>
    </CSS.AuthCard>
)

export default SignUp;