import React from "react";
import * as CSS from "../css";
import logo from "../../../Static/logo.svg";

const Qr = props => {
  const { callback } = props;
  return (
    <CSS.QRcode>
      <div className="Qrcode-title">扫码登录</div>
      <div className="Qrcode-content">
        <div className="Qrcode-img">
          <img src={logo} alt="Home Page" height="180" width="180"></img>
        </div>
        <p>
          打开
          <a
            href="https://www.zhihu.com/app/"
            target="_blank"
            rel="noopener noreferrer"
          >
            {" "}
            知乎 App
          </a>
        </p>
        <p>在「我的」页面右上角打开扫一扫</p>
        <button
          onClick={callback}
          style={{
            width: `50%`,
            height: `30px`,
            border: `none`,
            backgroundColor: `rgba(0, 0, 255, 0.1)`,
          }}
        >
          返回
        </button>
      </div>
    </CSS.QRcode>
  );
};
export default Qr;
