import React, { Component } from 'react';
import { Provider } from 'react-redux';
import store from './Store';
import Router from './Router';
import Global from './Pages/GlobalCSS';

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Global />
        <Router />
      </Provider>
    );
  }
}

export default App;
