import React from 'react';
import { BrowserRouter, Route, Redirect, HashRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import Index from './Pages/Home';
import Sign from './Pages/Sign';
import Test from './Pages/Test';

/**TODO:
 * 1. 使用Redirect包裹需要重定向的操作，而不是所有页面都需要登陆
 * 
 */
const Router = (props)=>(
    <HashRouter>
        <div>
            <Route path="/" exact component={()=>(
                props.login?<Index />:<Redirect to="/signin"/>
            )} />

            <Route path="/signin" exact component={Sign} />
            <Route path="/test" exact component={Test} />
            <Route path="/redirect">
                <Route path="/redirect/lost" component={()=>(
                    <Redirect to="/lost/findpwd"/>
                )}></Route>
            </Route>
        </div>
    </HashRouter>
);

const MapState = (state)=>{
    return{
        login: state.get('login'),
    }
};
export default connect(MapState, null)(Router);