import React, { Fragment } from 'react';
import {
    Err
} from '../css';

const InputWithErr = (props)=>(
    <Fragment>
        <input
            id={props.id}
            placeholder={props.input.placeholder || ''}
            type={props.input.type || 'text'}
            value={props.input.value || ''}
            onChange={props.input.onChange}
            onBlur={(e)=>{
                if(props.verification){
                    if(!props.verification.test(e.target.value)){
                        document.getElementById(`${props.id}Err`).style.display="flex";
                    }
                }else{
                    if(e.target.value === ''){
                        document.getElementById(`${props.id}Err`).style.display="flex";
                    }
                }
            }}
            autoFocus={props.input.autoFocus || false}
        />
        <Err 
            id={`${props.id}Err`}
            placeholder={props.err.placeholder || `${props.input.placeholder}错误`}
            onFocus={(e)=>{
                e.target.style.display = "none";
                document.getElementById(`${props.id}`).focus();
            }}
            readOnly
        />
    </Fragment>
)

export default InputWithErr;