import React from "react";
import * as CSS from "./css";
import logo from "../../Static/logo.svg";
import SignIn from "./SignIn/index";
import SignUp from "./SignUp";
import { withRouter } from "react-router-dom";

/**TODO:
 * 1. 修改为: Blur时直接报错
 * 2. 修改链接
 * 3. 其他页面
 * 4. 优化结构（至少不能太像知乎）
 */

/**FIXME:
 * 1. function export时怎么使用外层this
 * 2. 密码丢失时，怎么重定向
 * 3. 各种按钮的功能需要单独独立出来
 */

class Sign extends React.Component {
  verification = {
    phone: /^[1][0-9]{10}$/,
    verific: /^[0-9]{8}$/,
  };

  state = {
    focusd: false,
    eyeOn: false,
    switch: false,
    User: {
      id: "",
      pwd: "",
    },
  };

  options = [
    {
      href: "qrcode",
      info: "二维码登录",
    },
    {
      href: "##",
      info: "海外手机登录",
    },
    {
      href: "###",
      info: "社交帐号登录",
    },
  ];

  /**
   * @summary: 注册或登陆的提示信息
   */
  Switch = () => {
    let information;
    this.state.switch
      ? (information = (
          <div>
            没有帐号？
            <CSS.SwitchButton id="switch" href="#" onClick={this.ClickHandle}>
              注册
            </CSS.SwitchButton>
          </div>
        ))
      : (information = (
          <div>
            已有账号？
            <CSS.SwitchButton id="switch" href="#" onClick={this.ClickHandle}>
              登录
            </CSS.SwitchButton>
          </div>
        ));
    return information;
  };

  /**
   * @return: 返回 /Sign/Index.js下的options(其他登陆方式和链接)
   */
  OtherOption = () => {
    return this.options.map((items, index) => (
      <span key={index}>
        <button href={items.href} id={items.href} onClick={this.ClickHandle}>
          {items.info}
        </button>
      </span>
    ));
  };

  /**
   * @summary: 按钮的Click控制
   */
  ClickHandle = e => {
    // console.log(e.target);
    let id = e.target.id;
    switch (id) {
      case "message":
        //修改密码是否可见
        this.setState(preState => ({
          eyeOn: !preState.eyeOn,
        }));
        break;
      case "switch":
        //切换登陆或注册界面
        this.setState(preState => ({
          switch: !preState.switch,
        }));
        break;
      case "submit":
        //提交登陆或注册信息
        if (this.state.switch) {
          //登陆
          if (!this.verification.phone.test(this.state.User.id && true)) {
            //手机号或邮箱错误
            document.getElementById("idErr").style.display = "flex";
          } else {
          }
        } else {
          //注册
          if (!this.verification.phone.test(this.state.User.id)) {
            //手机号错误
            document.getElementById("idErr").style.display = "flex";
            document.getElementById("pwdErr").style.display = "flex";
          } else {
          }
        }
        break;
      case "lost":
        //更改验证码类型或忘记密码
        if (this.state.switch) {
          //登陆
          this.props.history.push(`/redirect/lost`);
        } else {
          //注册
          let inner = document.getElementById("message").innerHTML;
          document.getElementById(
            "message",
          ).innerHTML = document.getElementById("lost").innerHTML;
          document.getElementById("lost").innerHTML = inner;
        }
        break;
      case "email":
        this.props.history.replace({
          pathname: `/signin`,
          query: "email",
        });
        break;
      case "qrcode":
        this.props.history.replace({
          pathname: `/signin`,
          query: "qrcode",
        });
        break;
      default:
        console.warn(`id:${id} is Not ClickOptions.`);
    }
  };

  /**
   * @summary: Input控制
   */
  InputHandle = e => {
    let id = e.target.id;
    switch (id) {
      case "id":
      case "pwd":
        let { User } = this.state;
        User[id] = e.target.value;
        this.setState({
          User: User,
        });
        break;
      default:
        console.warn("inputChangeErr");
    }
  };

  render() {
    return (
      <CSS.Sign_Bg
        url={`url('https://static.zhihu.com/heifetz/assets/sign_bg.db29b0fb.png')`}
        color={`#b8e5f8`}
      >
        <CSS.Sign_Content>
          <CSS.LogoBanner>
            <div>
              <CSS.ToHomePage href="/">
                <img src={logo} alt="Home Page"></img>
              </CSS.ToHomePage>
            </div>
            Find More
          </CSS.LogoBanner>
          {this.state.switch ? (
            <SignIn
              verification={this.verification}
              state={this.state}
              InputCtrl={{
                InputHandle: this.InputHandle,
              }}
              OtherOption={this.OtherOption}
              Switch={this.Switch}
              ClickHandle={this.ClickHandle}
            />
          ) : (
            <SignUp
              verification={this.verification}
              state={this.state}
              Switch={this.Switch}
              InputCtrl={{
                InputHandle: this.InputHandle,
              }}
              ClickHandle={this.ClickHandle}
            />
          )}
        </CSS.Sign_Content>
        <CSS.Footer>
          <div>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://zhuanlan.zhihu.com"
            >
              知乎专栏
            </a>
            <a target="_blank" rel="noopener noreferrer" href="/roundtable">
              圆桌
            </a>
            <a target="_blank" rel="noopener noreferrer" href="/explore">
              发现
            </a>
            <a target="_blank" rel="noopener noreferrer" href="/app">
              移动应用
            </a>
            <a target="_blank" rel="noopener noreferrer" href="/contact">
              联系我们
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://app.mokahr.com/apply/zhihu"
            >
              来知乎工作
            </a>
            <a target="_blank" rel="noopener noreferrer" href="/org/signup">
              注册机构号
            </a>
          </div>
          <div>
            <span>© 2019知乎</span>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="http://www.miibeian.gov.cn/"
            >
              京 ICP 证 110745 号
            </a>
            <span>
              京公网安备 11010802010035 号
              <a
                href="https://zhstatic.zhihu.com/assets/zhihu/publish-license.jpg"
                target="_blank"
                rel="noopener noreferrer"
              >
                出版物经营许可证
              </a>
            </span>
          </div>
          <div>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://zhuanlan.zhihu.com/p/51068775"
              className="notlast"
            >
              侵权举报
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="http://www.12377.cn"
              className="notlast"
            >
              网上有害信息举报专区
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="/jubao"
              className="notlast"
            >
              儿童色情信息举报专区
            </a>
            <span>违法和不良信息举报：010-82716601</span>
          </div>
          <CSS.ZhengFuInfo>
            <div>
              <img
                src="https://static.zhihu.com/static/revved/img/index/chengxing_logo@2x.65dc76e8.png"
                alt="ZhengFu"
              ></img>
              <a href="https://credit.szfw.org/CX20170607038331320388.html">
                诚信网站示范企业
              </a>
            </div>
          </CSS.ZhengFuInfo>
        </CSS.Footer>
      </CSS.Sign_Bg>
    );
  }
}
export default withRouter(Sign);
